interface mapProps {
  scrollData: 'hidden' | 'fixed' | null;
}
const map = (props: mapProps) => ({
  'data-scrolled': props.scrollData,
});

export const TopBlock = styled.div.attrs(map)<mapProps>`
  position: absolute;
  z-index: 1;
  width: 100%;

  @media ${device.tablet} {
    width: 100%;
    display: flex;
    justify-content: flex-start;
  }

  &[data-scrolled='hidden'] {
    position: fixed;
    opacity: 0;
    min-height: 0;
  }

  &[data-scrolled='fixed'] {
    position: fixed;
    box-shadow: 0 2px 14px 0 rgba(0, 0, 0, 0.04),
      inset 0 -1px 0 0 rgba(0, 0, 0, 0.05);
    max-height: 95px;
    transition: 0.3s;
    background: #2a2a4f;
    @media ${device.tablet} {
      ${ButtonMenu} {
        display: flex;
      }
    }
  }
`;

