import * as React from 'react';
import {
  ButtonBlock,
  ButtonDescription,
  Cloud,
  CloudImg,
  Description,
  Header,
  TopBlock,
} from './style';
import { Button, Title, TextBody } from 'ui/atoms';
import { Menu } from './menu';
import { menu } from 'lib/constants';
import Clouds from 'assets/images/svg/clouds.svg';
import { bgStarMath, bgTabletStarMath } from 'assets/images/jpg';
import { Layout } from 'ui/templates';
import { Sidebar } from 'ui/molecules/sidebar';
import { DATA } from 'content';

export const StarMath: React.FC = () => {
  const [isVisible, setVisible] = React.useState(false);
  const [menuView, setMenuView] = React.useState<'hidden' | 'fixed' | null>(
    null,
  );

  React.useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const handleScroll = () => {
    const { scrollY } = window;
    if (scrollY > 270) {
      setMenuView('fixed');
    }
    if (scrollY > 120 && scrollY < 270) {
      setMenuView('hidden');
    }
    if (scrollY < 120) {
      setMenuView(null);
    }
  };
  return (
    <>
      <TopBlock scrollData={menuView}>
        <Menu items={menu} />
        <Sidebar
          isVisible={isVisible}
          toggler={() => {
            setVisible(!isVisible);
          }}
          menu={menu}
        />
      </TopBlock>
      <Layout
        background={bgStarMath}
        smallBackground={bgTabletStarMath}
        id="main"
      >
        <Cloud>
          <CloudImg src={Clouds} />
        </Cloud>
        <Header>
          <Title size="l">{DATA.starMathTitle}</Title>
        </Header>
        <Description>
          <TextBody color="basic">{DATA.starMathSubtitle}</TextBody>
        </Description>
        <ButtonBlock>
          <Button>
            <a href="#subscribe">Get the app for $2.99</a>
          </Button>
        </ButtonBlock>
        <ButtonDescription>
          <TextBody color="secondary" size="xs">
            One purchase for a year. / Math curriculum for kindergarten
          </TextBody>
        </ButtonDescription>
      </Layout>
    </>
  );
};
